import { supabase } from "../../supabaseClient";

//  ID     
async function SignIn(userID) {
  //userID:   id
  try {
    // UserTable  ID    
    const { data: user, error } = await supabase
      .from("UserTable")
      .select("gluten, dairy, egg, shellfish, nut, soy, fish, celery, mustard") //    (true or false)
      .eq("userID", userID)
      .maybeSingle();

    if (error) {
      console.error("Error checking userID:", error);
      return { error: "An error occurred while checking user ID" };
    }

    if (!user) {
      // UserTable.userID  id 
      return { error: "User ID not found" };
    }

    console.log("User signed in:", user); // UserTable.userID  id 
    return user; //    
  } catch (error) {
    console.error("Error:", error);
    return { error: "An error occurred during sign-in" };
  }
}

//  
SignIn("abc123").then((response) => {
  console.log(response);
});

export default SignIn;
