import React, { useState } from "react";
import "./StatusDisplay.css";
import CustomTextField from "../CustomComponent/CustomTextField";
import ReceiptSignIn from "../../controller/api/auth/ReceiptSignIn";
import { useGlobalData } from "../../context/DataContext";

const StatusDisplay = ({ handleArrowClick, setUserPoint }) => {
  const [inputValue, setInputValue] = useState("");
  const { setUsername, setAllergyData, receiptID, allergyData } =
    useGlobalData();
  const [error, setError] = useState("");

  const handleChange = (event) => {
    setInputValue(event.target.value);
  };

  const handleLoginClick = async () => {
    setError(""); //      
    const response = await ReceiptSignIn(
      inputValue,
      allergyData,
      receiptID.receiptID
    );
    console.log("ReceiptSignIn response:", response); //  
    if (response.error) {
      setError(response.error);
    } else {
      setUsername(inputValue);
      setAllergyData(response);
      console.log("Setting userPoint to:", response.coupon); //  
      setUserPoint(response.coupon); // userPoint 
      handleArrowClick();
    }
  };

  return (
    <div className='status-display'>
      <p className='status-text'>  /    ?</p>
      <div className='input-container'>
        <CustomTextField
          value={inputValue}
          onChange={handleChange}
          size='medium'
          backgroundColor='transparent'
          fontColor='white'
          placeholder='ID '
        />
        <button className='loginButton' onClick={handleLoginClick}>
          
        </button>
      </div>
      {error && <p className='error-text'>{error}</p>}
    </div>
  );
};

export default StatusDisplay;
